@import '../../globals.scss';

.movie-card {
    display: flex;
    max-height: 240px;
    background-color: $secondary-background;
    text-decoration: none;
    margin: 10px;
}

.movie-card-content {
    width: 100%;
    margin-left: 10px;
    margin-right: 10px;
    display: flex;
    align-items: center;
    justify-content: flex-start;
}


