@import '../../globals.scss';

.home-root {
  margin-top: $below-header;
  margin-bottom: $mobile-gutter;
  margin-left: $mobile-gutter;
  margin-right: $mobile-gutter;
}

.landing-page-card {
  @extend %center-children;
  height: calc((100vh - 300px) / 3);
  min-height: 120px;
  width: 100%;
  background-color: $secondary-background;
  text-decoration: none;
  font-size: 24px;
}


