@import '../../globals.scss';

$mobile-gutter: 40px;

.media-details-root {
  margin-top: $below-header;
  margin-bottom: $mobile-gutter;
  margin-left: $mobile-gutter;
  margin-right: $mobile-gutter;
}

.media-details-title h3 {
  margin-left: 15px;
}

.media-information {
  justify-content: center;
  display: flex;
  flex-wrap: wrap;
}

.media-information div {
  width: 80%;
  justify-content: center;
}

.media-poster {
  display: flex;
  width: 100%;
  justify-content: center;
}


